.cursesProgresIcon {
  position: relative;
  width: 4.06rem;
  height: 4.34rem;
}
.div {
  top: 0;
  left: 0;
  font-weight: 600;
}
.div,
.div1,
.parent {
  position: absolute;
}
.div1 {
  top: 0.09rem;
  left: 1rem;
  font-size: var(--default-text-list-size);
  color: var(--white-50);
}
.parent {
  top: 0;
  left: 0;
  width: 2.81rem;
  height: 1.44rem;
}
.coursesCompleted {
  position: absolute;
  top: 1.44rem;
  left: 0;
  font-size: var(--menulink-size);
  color: var(--white-50);
}
.groupParent {
  position: absolute;
  top: 0;
  left: 0;
  width: 7.63rem;
  height: 2.44rem;
}
.blockCursesProgresInner {
  position: relative;
  width: 7.63rem;
  height: 2.44rem;
}
.blockCursesProgres {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: var(--br-3xs);
  background-color: var(--filters);
  width: 17.06rem;
  display: flex;
  flex-direction: row;
  padding: var(--padding-base) var(--padding-5xl);
  box-sizing: border-box;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-5xl);
  text-align: left;
  font-size: var(--courses-completed-size);
  color: var(--white);
  font-family: var(--default-text-list);
}
