.iconlylightsearch {
  width: 1.5rem;
  height: 1.5rem;
}
.iconlylightsearch,
.search1 {
  position: relative;
  opacity: 0.3;
}
.search {
  border-radius: var(--br-3xs);
  background-color: var(--avatar-bg-color);
  width: 23.75rem;
  height: 2.75rem;
  display: flex;
  flex-direction: row;
  padding: var(--padding-5xs) var(--padding-base);
  box-sizing: border-box;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-base);
}
.en {
  position: relative;
}
.iconlylightarrowDown2 {
  position: relative;
  width: 1rem;
  height: 1rem;
}
.enParent {
  position: absolute;
  top: 0.88rem;
  left: 0.75rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--gap-8xs);
}
.frameWrapper {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: var(--br-3xs);
  overflow: hidden;
}
.notificationIcon,
.notifylangInner {
  position: relative;
  border-radius: var(--br-3xs);
  width: 4.13rem;
  height: 3rem;
}
.notificationIcon {
  width: 3rem;
  overflow: hidden;
  flex-shrink: 0;
}
.notifylang,
.themelangnotify {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.notifylang {
  height: 3rem;
  justify-content: center;
  gap: var(--gap-5xs);
}
.themelangnotify {
  justify-content: flex-start;
}
.peter {
  position: relative;
  line-height: 100%;
  font-weight: 500;
}
.peterWrapper {
  flex-direction: row;
  justify-content: flex-start;
}
.adminWrapper,
.nameSpeciality,
.peterWrapper {
  display: flex;
  align-items: flex-start;
}
.adminWrapper {
  align-self: stretch;
  flex-direction: row;
  justify-content: flex-start;
  font-size: var(--font-size-xs);
  color: var(--menulink);
}
.nameSpeciality {
  flex-direction: column;
  justify-content: center;
  gap: var(--gap-9xs);
}
.avatar40pxIcon {
  position: relative;
  width: 2.5rem;
  height: 2.5rem;
  overflow: hidden;
  flex-shrink: 0;
}
.profile,
.themelangnotifyprofile {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.profile {
  padding: var(--padding-9xs) var(--padding-3xs);
  justify-content: flex-start;
  gap: var(--gap-xs);
  font-size: var(--menulink-size);
  color: var(--white);
}
.themelangnotifyprofile {
  height: 3rem;
  justify-content: center;
  gap: var(--gap-5xl);
  color: var(--color-gray-100);
}
.header {
  position: absolute;
  top: 0;
  left: 14.81rem;
  background-color: var(--filters);
  width: 75.19rem;
  height: 4.25rem;
  display: flex;
  flex-direction: row;
  padding: var(--padding-3xs) var(--padding-5xl);
  box-sizing: border-box;
  align-items: flex-start;
  justify-content: space-between;
  text-align: left;
  font-size: var(--default-text-list-size);
  color: var(--white);
  font-family: var(--default-text-list);
}
